<template>
    <div class="header">
      <slot>顺丰区块链追溯</slot>
        <i class="iconfont icon-fanhui" v-if="back" @click="goback"><span>返回</span></i>
    </div>
</template>
<script>

    export default{
        props:{
          back:{
            type:Boolean,
            default:false,
          }
        },
        data(){
            return {}
        },
        computed: {},
        components: {

        },
        methods: {
            goback(){
                this.$router.go(-1);
            }
        },
    }
</script>
<style scoped lang="less">
  .header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: black;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-weight: bold;
    color: white;
    i{
      font-style: normal;
      position: absolute;
      left: 10px;
      font-size: 15px;
      color: white;
    }
  }
</style>



